import React from 'react';

import './customers.css';

class Customers extends React.Component {
  constructor(){
      super();
      this.state ={
          customers:[]
        };
  }
  componentDidMount(){
      fetch('/api/customers')
      .then(res =>res.json())
      .then(customers =>this.setState({customers},() =>{console.log('customers fetched',customers)}));
  }
    render(){
    return (
    <div>
      <h2>Customers</h2>
      <ul>
          {this.state.customers.map(customers =><li key={customers.id}> {customers.firstName}</li>)}
          
      </ul>
    </div>
  );
}
}

export default Customers;